import React, { useState, useEffect } from 'react';
import { PageHeader, Descriptions, Spin, Table } from 'antd';
import { useHistory, useRouteMatch } from 'react-router-dom';
import moment from 'moment';

// APIs
import { getPatientDetailsAPI } from 'services/user/patient.service';
import { listEMRAPI } from 'services/user/emr.service';

const { Item } = Descriptions;

const PatientDetails = props => {

  const { goBack } = useHistory();
  const { params } = useRouteMatch();
  const patientId = params.patientId;

  const [patient, setPatient] = useState(null);
  const [patientLoading, setPatientLoading] = useState(false);
  const [EMRs, setEMRs] = useState(null);
  const [EMRLoading, setEMRLoading] = useState(false);

  const tableColumns = [
    {
      title: '#',
      key: '#',
      render: (text, record, index) => index + 1
    },
    {
      title: 'Ngy khm',
      key: 'created_at',
      render: (text, record) => moment(record.created_at).format('DD/MM/YYYY HH:mm')
    },
    {
      title: 'Gi kt thc',
      key: 'completed_at'
    },
    {
      title: 'Bc s',
      key: 'physician',
      render: (text, { physician }) => physician.first_name + " " + physician.last_name
    },
    {
      title: 'Chn on',
      key: 'diagnose',
      render: (text, { emr_diseases }) => emr_diseases.map(disease => disease.name).join(", ")
    }
  ]

  async function getPatientDetails(patientId) {
    try {
      setPatientLoading(true);
      const patientResponse = await getPatientDetailsAPI(patientId);
      setPatient(patientResponse.data);
    } catch (error) {
      console.log(error);
    } finally {
      setPatientLoading(false);
    }
  }

  async function getPatientEMRs(patientId) {
    try {
      setEMRLoading(true);
      const EMRResponse = await listEMRAPI({ patient_id: patientId });
      setEMRs(EMRResponse.data);
    } catch (error) {
      console.log(error);
    } finally {
      setEMRLoading(false);
    }
  }

  async function getInitialData() {
    await Promise.all([
      getPatientDetails(patientId),
      getPatientEMRs(patientId)
    ])
  }

  useEffect(() => {
    getInitialData();
  }, []);

  return (
    <>
      <PageHeader onBack={goBack} title={patient ? patient.first_name + " " + patient.last_name : "Chi tit bnh nhn"} />

      <Spin spinning={patientLoading}>
        <Descriptions title="Thng tin c nhn" colon>
          <Item label="H v tn">{patient ? patient.first_name + " " + patient.last_name : "ang ti..."}</Item>
          <Item label="Gii tnh">{patient?.gender || "ang ti"}</Item>
          <Item label="Ngy sinh">{patient ? moment(patient.DOB).format("DD/MM/YYYY") : "ang ti"}</Item>
          <Item label="in thoi">{patient?.phone || "ang ti"}</Item>
          <Item label="a ch">{patient?.address || "ang cp nht"}</Item>
        </Descriptions>
      </Spin>

      <div className="ant-descriptions-title mb-3">Lch s khm bnh</div>
      <Table rowKey="id" dataSource={EMRs?.results} loading={EMRLoading} columns={tableColumns} />

    </>
  )
}

export default PatientDetails;